@import url("https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap");
/* global styling */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Spartan", sans-serif;
}

#root {
  height: 100%;
}

a {
  text-decoration: none;
  color: #222;
  display: block;
}

html {
  scroll-behavior: smooth;
}

/* Global Styles */

h1 {
  font-size: 50px;
  line-height: 64px;
  color: #222;
}

h2 {
  font-size: 46px;
  line-height: 54px;
  color: #222;
}

h4 {
  font-size: 20px;
  color: #222;
}

h6 {
  font-weight: 700;
  font-size: 12px;
}

button.white {
  padding: 11px 18px;
  font-size: 13px;
  font-weight: 600;
  color: #fff;
  background-color: transparent;
  border: 1px solid #fff;
  cursor: pointer;
  transition: 0.2s;
  border-radius: 2px;
}

button.white:hover {
  background-color: #088178;
  border: none;
}

p {
  font-size: 16px;
  color: #465b52;
  margin: 15px 0 20px 0;
}

.section-p1 {
  padding: 40px 80px;
}

.section-m1 {
  margin: 40px 0;
}

html,
body {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  height: 100%;
}

ul {
  list-style: none;
}

.App {
  display: flex;
  flex-direction: column;
  height: 100%;
}
