#footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #003060;
  color: white;
  height: 100px;
  width: 100%;
  padding: 10px 0;
}

#footer h2 {
  font-size: 30px;
  font-weight: 600;
  color: #fff;
}

#footer h4 {
display: flex;
gap: 0.5rem;
}

#footer h4 a {
  color: #ff9898;
  text-decoration: none;
  transition: 0.2s;
}

#footer h4 {
  font-size: 20px;
  font-weight: 600;
  color: #fff;
}


#footer button {
  padding: 15px 30px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.2s;
  border: none;
  border-radius: 2px;
}

#footer button:hover {
  background-color: #088178;
  color: #fff;
}