#header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 80px;
  background-color: #e3e6f3;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.06);
  z-index: 999;
  position: sticky;
  top: 0;
  left: 0;
}

#headerUl {
  display: flex;
  gap: 2rem;
  transition: 0.3s ease;
}

#headerUl li {
  position: relative;
}

#headerUl li a {
  text-decoration: None;
  color: #1a1a1a;
  font: 16px;
  font-weight: 600;
  transition: 0.3s ease;
}

#headerUl li a:hover,
#headerUl li a.active {
  color: #088178;
}

#headerUl li a.active::after {
  content: "";
  width: 40%;
  height: 2px;
  position: absolute;
  bottom: -4px;
  left: 0px;
  background: #088178;
}

.mobile {
  display: none;
}

#headerUl #close {
  display: none;
}

@media only screen and (max-width: 769px) and (min-width: 400px) {
  #header {
    padding: 10px 30px;
  }

  #headerUl {
    flex-direction: column;
    height: 100vh;
    background-color: #d8dcf3;
    position: fixed;
    width: 300px;
    top: 0;
    right: -300px;
    padding: 30px 20px;
    box-shadow: 0px 40px 60px rgba(0, 0, 0, 0.1);
  }

  #headerUl #close {
    display: inline;
    font-size: 22px;
  }

  #cart {
    display: none;
  }

  .mobile {
    display: flex;
    gap: 20px;
    margin-right: 20px;
  }
}

@media only screen and (max-width: 400px) {
  /* copy pasted from ipad media query */
  #header {
    padding: 10px 40px;
  }

  #headerUl {
    flex-direction: column;
    height: 100vh;
    background-color: #d8dcf3;
