    right: -200px;
    padding: 30px 20px;
    box-shadow: 0px 40px 60px rgba(0, 0, 0, 0.1);
  }

  #headerUl #close {
    display: inline;
    font-size: 22px;
  }

  #cart {
    display: none;
  }

  .mobile {
    display: flex;
    gap: 20px;
    margin-right: 20px;
  }
}
